        </Breadcrumb>
        <LeftSidebar :open="false">
            <Nav />
        </LeftSidebar>
        <Main :withoutRight="true" :withoutLeft="true">
            <div class="m-facedata">
                <div class="m-face-parse">
                    <h1 class="m-face-parse-title">脸型妆容数据解析器</h1>
                    <div class="m-face-parse-upload" v-if="support">
                        <input
                            class="u-input"
                            type="file"
                            id="face_file"
                            @change="uploadData"
                        />
                        <p class="u-status" v-if="!data">
                            请上传脸型数据，如何导出数据请查看<a
                                href="/tool/?pid=746"
                                target="_blank"
                                >《捏脸数据导入导出方法》</a
                            >
                        </p>
                        <p class="u-status" v-else>
                            {{ data.name }}
                        </p>
                        <el-button
                            class="u-btn"
                            type="primary"
                            @click="selectData"
                            icon="el-icon-upload2"
                            >上传脸型数据</el-button
                        >
                    </div>
                    <el-alert
                        v-else
                        class="m-face-parse-notsupport"
                        title="浏览器不支持"
                        type="error"
                        description="你的浏览器太老旧不支持本地解析,请更换chrome或其它现代浏览器"
                        show-icon
                    >
                    </el-alert>

                    <div class="m-face-parse-preview">
                        <result v-if="facedata" :data="facedata" />
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </Main>
    </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import { uploadData, parseData } from "../../service/facedata.js";
import result from "./result.vue";

export default {
    name: "Facedata",
    props: [],
    data: function() {
        return {
            data: "",
            support: !!FileReader,
            facedata: "",
        };
    },
    computed: {},
    methods: {
        // 数据
        selectData: function(i) {
            let fileInput = document.getElementById("face_file");
            fileInput.dispatchEvent(new MouseEvent("click"));
        },
        uploadData: function(e) {
            // let formdata = new FormData();
            let file = e.target.files[0];
            this.data = file;
            // formdata.append("file", file);
            // uploadData(formdata, this).then((res) => {
            //     this.data = res.data.data.list[0];
            //     this.$message({
            //         message: res.data.msg,
            //         type: "success",
            //     });
            // });
            this.parseData(file);
        },
        // 解析数据
        parseData: function(facedata) {
            const vm = this;

            let fr = new FileReader();
            fr.readAsText(facedata);
            fr.onload = function(e) {
                console.log("读取成功...开始执行分析...");
                parseData(e.target.result, vm)
                    .then((res) => {
                        vm.facedata = JSON.stringify(res.data.data);
                        return res.data.status;
                    })
                    .then((status) => {
                        if (status) {
                            vm.$notify({
                                title: "成功",
                                message: "脸型数据解析成功",
                                type: "success",
                            });
                        } else {
                            vm.$notify.error({
                                title: "错误",
                                message: "无法解析脸型数据",
                            });
                        }
                    });
            };
            fr.onerror = function(e) {
                vm.$notify.error({
                    title: "错误",
                    message: "无法解析脸型数据",
                });
            };
        },
    },
    filters: {},
    created: function() {},
    components: {
        result,
        Nav,
    },
};
</script>

